/*global cordova*/
import axios from 'axios';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import React, { useContext, useEffect, useState } from 'react';
import { ListGroup, ListGroupItem, Nav, NavItem, NavLink, TabContent, TabPane } from 'reactstrap';
import { todayNewsApi } from '../../api/live';
import { MarketContext } from '../../context/contexts';

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};


function DefaultAside(props) {


  const [activeTab, setActiveTab] = useState('1');
  const [news, setNews] = useState([]);
  const {market} = useContext(MarketContext);

  useEffect(() => {
    let fetchNewsId = -1;
    fetchNews();
    if (market == 'Live') {
      fetchNewsId = setInterval(fetchNews, 300000);
      return () => {
        clearInterval(fetchNewsId)
      }
    }
  }, []);

  const toggle = tab => {
    if (activeTab !== tab) {
      setActiveTab(tab);
    }
  }

  const fetchNews = () => {
    axios.all([axios(todayNewsApi)]).
      then(axios.spread(todayNews => {
        setNews(todayNews.data);
      }));
  }

  const newsDropdown = news.map((news, index) =>
    <ListGroupItem key={index} action tag="a" target="_blank" href={news.url} className="list-group-item-accent-warning list-group-item-divider">
      <div>{news.title}  </div>
      <small className="text-muted mr-3">
        <i className="icon-calendar"></i>&nbsp; 11am - 3pm
                </small>
    </ListGroupItem>);


  // eslint-disable-next-line
  const { children, ...attributes } = props;


  return (
    <React.Fragment>
      <Nav tabs>
        <NavItem>
          <NavLink className={classNames({ active: activeTab === '1' })}
            onClick={() => {
              toggle('1');
            }}>
            <i title="Latest News" className="fa fa-newspaper-o"></i>
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink className={classNames({ active: activeTab === '2' })}
            onClick={() => {
              toggle('2');
            }}>
            <i className="fa fa-briefcase"></i>
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink className={classNames({ active: activeTab === '3' })}
            onClick={() => {
              toggle('3');
            }}>
            <i className="fa fa-line-chart"></i>
          </NavLink>
        </NavItem>
      </Nav>
      <TabContent activeTab={activeTab}>
        <TabPane tabId="1">
          <ListGroup className="list-group-accent overflow-y-auto" tag={'div'}>
            <ListGroupItem className="list-group-item-accent-secondary bg-light text-center font-weight-bold text-muted text-uppercase small">Latest News</ListGroupItem>
            {newsDropdown.length > 0 ? newsDropdown : <ListGroupItem className="list-group-item-accent-danger list-group-item-divider">
              <div> <strong>Check again later... <i className="cui-envelope-open"></i></strong></div>
              <small className="text-muted mr-3"> No new news today</small>
              <small className="text-muted"></small>
            </ListGroupItem>}
          </ListGroup>
        </TabPane>
        <TabPane tabId="2" className="p-3">
          <ListGroup className="list-group-accent" tag={'div'}>
            <ListGroupItem className="list-group-item-accent-secondary bg-light text-center font-weight-bold text-muted text-uppercase small">Fundamental Watchlist</ListGroupItem>
            <ListGroupItem action tag="a" href="/login" className="list-group-item-accent-danger list-group-item-divider">
              <div> <strong>Coming soon...</strong></div>
              <small className="text-muted mr-3"> Stay tuned for updates </small>
              <small className="text-muted"><i className="icon-home"></i></small>
            </ListGroupItem>
          </ListGroup>
        </TabPane>
        <TabPane tabId="3" className="p-3">
          <ListGroup className="list-group-accent" tag={'div'}>
            <ListGroupItem className="list-group-item-accent-secondary bg-light text-center font-weight-bold text-muted text-uppercase small">Technical Watchlist</ListGroupItem>
            <ListGroupItem action tag="a" href="/login" className="list-group-item-accent-danger list-group-item-divider">
              <div> <strong>Coming soon...</strong></div>
              <small className="text-muted mr-3"> Stay tuned for updates </small>
              <small className="text-muted"><i className="icon-home"></i></small>
            </ListGroupItem>
          </ListGroup>
        </TabPane>
      </TabContent>
    </React.Fragment>
  );
}


DefaultAside.propTypes = propTypes;
DefaultAside.defaultProps = defaultProps;

export default DefaultAside;
